/* Font ============================== */
// @import url('https://fonts.googleapis.com/css2?family=Kalam:wght@700&display=swap');

/* Global ============================== */
body {
  background-color: #121212;
  scroll-behavior: smooth;
  color: white;
  display: flex;
  justify-content: center;
  // font: bold 1em monospace;
}

.container {
  padding: 0 15px;
}

/* Custom Scroll Bar ============================== */
// For Firefox
:root {
  scrollbar-color: #666 #222 !important;
  scrollbar-width: thin !important;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #222;
}

::-webkit-scrollbar-thumb {
  background-color: #666;
  border-radius: 50px;
}

/* ==================== Todo App ===================================*/
.todo-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  /* Add Task ============================== */
  .add-task {
    margin-top: 20px;
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;

    input {
      all: unset;
      border: 2px solid skyblue;
      box-shadow: 5px 5px palevioletred, -5px -5px powderblue;
      border-radius: 3px;
      width: 600px;
      color: white;
      font: bold 1.7em monospace;
      padding: 20px 20px;
      margin-right: 20px;
      letter-spacing: 0.4px;
    }

    img {
      width: 55px;
      cursor: pointer;
      transition: 0.1s;
    }
  }

  /* Tasks-State ============================== */
  .tasks-state {
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font: bold 1.1em sans-serif;
    margin-bottom: 50px;
    margin-left: -50px;
    background: linear-gradient(to left, #ff9966, #ff5e62);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  /* Tasks Content ============================== */
  .tasks-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left: -100px;

    .no-task {
      background: linear-gradient(to left, gold, orange);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font: bold 5em monospace;
      margin-top: 100px;
      margin-left: 70px;
      text-transform: capitalize;
    }

    .task-box {
      width: 330px;
      border-radius: 3px;
      background: linear-gradient(to right, #ff9966, #ff5e62);
      color: #000;
      font: bold 2em monospace;
      margin-right: 20px;
      display: flex;
      justify-content: start;
      align-items: center;
      margin-bottom: 20px;
      position: relative;
      cursor: pointer;
      padding: 20px;
      letter-spacing: 0.3px;

      img {
        width: 50px;
        cursor: pointer;
        transition: 0.1s;
        position: absolute;
        right: -140px;
      }
    }
    .comleted {
      opacity: 0.3;
    }
  }
}

//Sweet alert ================================================
// Body
.swal2-popup.swal2-modal.swal2-icon-warning.swal2-show {
  background-color: gold;
}

// Text
div#swal2-content {
  font: bold 2em monospace;
  color: #000;
}

// Icon
.swal2-icon.swal2-warning.swal2-icon-show {
  border-color: #000;
  color: #000;
}

// Button
button.swal2-confirm.swal2-styled {
  all: unset;
  background-color: white;
  color: black;
  border-radius: 50px;
  width: 100px;
  height: 30px;
  text-align: center;
  font: bold 1.5em sans-serif;
  padding: 10px;
  cursor: pointer;
}
